.nav-bar {
  position: fixed;
  z-index: 2;
  top: 4.4rem;
  border: 0.1rem solid #e0e0e0;
  background-color: #fff; }
  .nav-bar .scroll {
    height: 3rem;
    width: 100vw;
    overflow-x: scroll; }
    .nav-bar .scroll::-webkit-scrollbar {
      display: none; }
    .nav-bar .scroll .wrapperList {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      line-height: 3rem; }
      .nav-bar .scroll .wrapperList .item {
        width: 6rem;
        font-size: 1.3rem;
        font-weight: normal;
        text-align: center; }
      .nav-bar .scroll .wrapperList .current {
        color: #d58f75; }
